import { Table, Button } from 'antd';
import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { IAction, IStoreState } from '@/types';
import { connect } from 'react-redux';
import { IProjectInfo } from '@/api';

interface Props {
  projectInfo: IProjectInfo;
}

const columns = [
  {
    title: '',
    dataIndex: 'nickname',
    key: 'nickname'
  },
  {
    title: '',
    dataIndex: 'username',
    key: 'username'
  },
  {
    title: '',
    dataIndex: 'roleCode',
    key: 'roleCode'
  },
  {
    title: '',
    key: 'action',
    render: (text: any, record: any) => (
      <span>
        <button></button>
        <button></button>
      </span>
    )
  }
];

const ProjectMember = ({ projectInfo }: Props) => {
  return (
    <div>
      <Button></Button>
      <Table columns={columns} dataSource={projectInfo.members} />
    </div>
  );
};

const mapStateToProps = (state: IStoreState) => {
  const { projectInfo } = state.project;
  return {
    projectInfo
  };
};

const mapDispatchToProps = (dispatch: Dispatch<IAction>) => bindActionCreators({}, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProjectMember);
